.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 33px;
  position: relative;
  font-size: 16px;
  font-weight: bold;
  border: 2px solid #006dfe;
  height: 66px;
  cursor: pointer;
  transition: all 0.1s ease-in;
}
.btn:after {
  content: '';
  display: block;
  position: absolute;
  left: 100%;
  margin-left: -5px;
  top: 28px;
  width: 10px;
  height: 27px;
  background: #006dfe;
  transition: all 0.1s ease-in;
}
.btn svg {
  display: block;
  margin-right: 28px;
  transition: all 0.1s ease-in;
}
.btn:hover,
.btn--active {
  color: #fff;
  background: #006dfe;
}
.btn:hover svg,
.btn--active svg {
  fill: #fff;
}
.btn--lineIcon:before {
  content: '';
  display: block;
  width: 35px;
  height: 4px;
  background: #006dfe;
  margin-right: 24px;
  transition: all 0.1s ease-in;
}
.btn--lineIcon:hover:before,
.btn--lineIcon--active {
  background: #1d1d1d;
}
